import React, { Component } from 'react';
import { Container } from 'react-bootstrap';
import classNames from 'classnames';
import moment from 'moment';

// Assets
import icons from '../../shared/icons';

// Styles
import './header.css';

class Header extends Component {
  constructor(props) {
    super(props);

    this.navElem = React.createRef();

    this.state = {
      isSticky: false,
      isNavbarShow: false,
    };
  }

  componentDidMount() {
    window.addEventListener('scroll', event => {
      let supportPageOffset = window.pageXOffset !== undefined;
      let isCSS1Compat = (document.compatMode || '') === 'CSS1Compat';
      let scroll = {
        x: supportPageOffset
          ? window.pageXOffset
          : isCSS1Compat
          ? document.documentElement.scrollLeft
          : document.body.scrollLeft,
        y: supportPageOffset
          ? window.pageYOffset
          : isCSS1Compat
          ? document.documentElement.scrollTop
          : document.body.scrollTop,
      };

      /* if (scroll.y > this.navElem.current.offsetTop) {
        this.setState({
          isSticky: true,
        });
      } else {
        this.setState({
          isSticky: false,
        });
      } */
    });
  }

  toggleNavbar = () => {
    this.setState({
      isNavbarShow: this.state.isNavbarShow ? false : true,
    });
  };

  render() {
    let navClasses = classNames({
      navbar: true,
      'navbar-expand-lg': true,
      sticky: this.state.isSticky,
    });

    let navbarCollapse = classNames({
      collapse: true,
      'navbar-collapse': true,
      show: this.state.isNavbarShow,
    });

    let currentTime = moment().format('ddd LT');

    return (
      <header ref={this.navElem} className="header">
        <nav className={navClasses}>
          <Container>
            <a href="/" className="header-left">
              <img className="brand-logo" src={icons['apple']} alt="apple" />
              <div>Gustavo Lee</div>
            </a>
            <div className="header-right">{currentTime}</div>
          </Container>
        </nav>
      </header>
    );
  }
}

export default Header;
